/* _sass/invasion/_header.scss */
@use "invasion/prelude" as *;

/* Scroll to top */
#top {
  position: absolute;
  top: 0;
}

/* Site header
==================== */

.preheader {
  color: $on-secondary;
  font-family: "Roboto", sans-serif;
  font-size: $spacing-unit*1.4;
  font-weight: 600;
  line-height: 2;
  text-align: center;
  margin-bottom: 0;

  p {
    line-height: 1.2;
    margin-bottom: 0;
    padding: 6px;
  }
  a,a:visited {
    color: $on-secondary;
    font-weight: 900;
    text-decoration: none;

    &:hover,&:focus { color: $tertiary; }
  }
}

.alt-body .preheader {
  background-color: $primary;
  color: $secondary;

  a,a:visited {
    color: $secondary;
    &:hover,&:focus { color: $on-primary-link-hover; }
  }
}

.subheader {
  background-color: $primary;
  color: $white;
  font-family: "Anton", sans-serif;
  font-size: $spacing-unit*1.6;
  height: $spacing-unit*3;
  line-height: 2;
  margin-bottom: 0;
  position: absolute;
  text-align: center;
  top: 30px;
  width: 100%;
  z-index: 30;
}
.sub1 {
  display: block;
  @media screen and (max-width:370px) { display: none; }
}
.sub2 {
  display: none;
  @media screen and (max-width:370px) { display: block; }
}

/**
 * Site header
 */
.site-header {
  color: $black;
  font-family: "Freeman", sans-serif;
  font-weight: 400;
  font-style: normal;
  min-height: $spacing-unit * 1.865;
  text-transform: uppercase;
  position: relative;
}

.site-title {
  background-size: contain;
  float: left;
  height: $spacing-unit*4;
  margin: $spacing-unit 0 $spacing-unit half($spacing-unit);
  text-indent: -99999px;
  width: 400px;

  @include relative-font-size(1.625);

  @include media-query ($on-500){
    background-size: contain;
    margin-left: $spacing-unit;
    width: $spacing-unit*10;
  }
}

.site-nav {
  float: right;
  line-height: $base-line-height * $base-font-size * 2.25;
  z-index: 10;

  .nav-trigger { display: none; }

  .menu-icon, .close-icon { display: none; }

  .page-link {
    color: $primary;
    line-height: $base-line-height;
    text-decoration: none;

    &:not(:last-child) { margin-right: 20px; }
    &:hover,&:focus { color: $on-primary-link-hover; text-decoration: none; }
  }

  @include media-query($on-1000) {
    position: absolute;
    top: 9px;
    right: half($spacing-unit);
    text-align: center;

    label[for="nav-trigger"] {
      display: block;
      float: right;
      margin-bottom: 12px;
      width: 36px;
      height: 36px;
      z-index: 2;
      cursor: pointer;

      .menu-icon {
        display: block;
        float: right;
        width: 36px;
        height: 26px;
        line-height: 0;
        padding-top: 10px;
        text-align: center;

        > svg { fill: $white; }
      }

      .close-icon {
        display: none;
        float: right;
        width: 36px;
        height: 26px;
        line-height: 0;
        padding-top: 10px;
        text-align: center;

        > svg { fill: $white; }
      }
    }
    .nav-trigger:checked + .nav-label {
      .menu-icon { display: none; }
      .close-icon { display: block; }
    }

    input ~ .trigger { clear: both; display: none; }

    input:checked ~ .trigger {
      background: rgba(0,0,0,0.9);
      display: block;
      font-size: 2rem;
      border: 1px solid $white;
      padding: $spacing-unit*3 $spacing-unit $spacing-unit*6;
      width: 100vw;
      position: absolute;
      right: -5px;
      top: 50px;

      @include media-query($on-1000) { min-height: 50vh; }
      .menu-icon { display: none; }
      .close-icon { display: block; }
    }

    .page-link {
      display: block;
      padding: 5px 10px;
      &:not(:last-child) { margin-right: 0; }
      margin-left: 20px;
    }
  }
}

.index-header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  min-width: 100%;
  padding: 0;
  position: absolute;
  z-index: 4;

  .site-title { display: none; }

  .page-link {
    color: $white;

    &:hover,&:focus { color: $secondary; }
    @include media-query($on-1000) {
      &:hover,&:focus { color: $secondary; }
    }
  }

  @include media-query($on-1000) {
    label[for="nav-trigger"] {
      margin-top: 12px;

      .menu-icon  > svg { fill: $white; }
      .close-icon > svg { fill: $white; }
    }
    .site-nav { position: static; }
  }
}

.primary-header {
  background-color: #00A650;
  background-image: url(/assets/svg/cirlces-light.svg);
  background-position: top;
  background-size: cover;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  min-width: 100%;
  padding: 0;
  position: absolute;
  z-index: 4;

  .wrapper { width: 100%; }

  .site-title {
    background: url(/assets/logo/ic-lockup-horizontal-alt.svg) no-repeat;

    @include relative-font-size(1.625);

    @include media-query ($on-500){
      background: url(/assets/logo/ic-logo.svg) no-repeat;
    }
  }

  .page-link {
    color: $white;

    &:hover,&:focus { color: $secondary; }
    @include media-query($on-1000) {
      &:hover,&:focus { color: $secondary; }
    }
  }

  @include media-query($on-1000) {
    label[for="nav-trigger"] {
      margin-top: 12px;

      .menu-icon  > svg { fill: $white; }
      .close-icon > svg { fill: $white; }
    }
    .site-nav { position: static; }
  }
}

.secondary-header {
  .site-title {
    background: url(/images/logo/IC-LOGO-FULL-2024-2.svg) no-repeat;

    @include relative-font-size(1.625);

    @include media-query ($on-500){
      background: url(/images/logo/IC-LOGO-SHORT-2024.svg) no-repeat;
    }
  }
  .page-link {
    color: $primary;

    &:hover,&:focus { color: $on-primary-link-hover; }
    @include media-query($on-1000) {
      &:hover,&:focus { color: $on-primary-link-hover; }
    }
  }

  @include media-query($on-1000) {
    label[for="nav-trigger"] {
      .menu-icon  > svg { fill: $primary; }
      .close-icon > svg { fill: $primary; }
    }
  }
}
